import React from "react";

//component
import { Container, Row, Col, Image, Button, Badge } from "react-bootstrap";
import { v4 as uuidv4 } from "uuid";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faClock, faLocationPin } from "@fortawesome/free-solid-svg-icons";
import { useNavigate } from "react-router-dom";
import { isMobile } from "react-device-detect";
import dateFormat from "dateformat";

export default function CardsEventPage({ data, onSubmit }) {
  const navigate = useNavigate();

  const eventGoTo = (e) => {
    e.preventDefault();
    navigate("/Events/EventDetails", { state: data });
  };

  return (
    <>
      <Container fluid className={`h-auto m-0 my-5`}>
        <Row className="m-0 p-0 w-100 h-100 justify-content-center">
          <Col lg={1} className="d-lg-block d-none">
            {/*Date box*/}
            <Container className="event-date-box w-100 p-0 m-0">
              <Row className="h-100 w-100 p-0 m-0">
                <Col lg={12} className="h-75 text-center">
                  <h1 className="event-date-day m-0">
                    {dateFormat(data.content_schedule, "dd")}
                  </h1>
                </Col>
                <Col lg={12} className="h-25 text-center">
                  <h1 className="event-date-month-year m-0">
                    {dateFormat(data.content_schedule, "mmm")}{" "}
                    {dateFormat(data.content_schedule, "yyyy")}
                  </h1>
                </Col>
              </Row>
            </Container>
          </Col>
          <Col lg={4}>
            {/*Image box*/}
            <Row className="m-0 p-0 w-100 h-100">
              <Col lg={12} className="m-0 p-0">
                <Image
                  className="h-auto w-100"
                  src={
                    process.env.REACT_APP_API_URL +
                    "/images/contents/" +
                    data.content_thumbnail
                  }
                />
              </Col>
            </Row>
          </Col>
          <Col
            lg={5}
            className="m-0 p-0 px-3 event-container-border border-animation"
          >
            <Row className="m-0 p-0 w-100 h-auto">
              <Col
                lg={12}
                className="m-0 p-0 d-flex align-items-center justify-content-center justify-content-lg-start"
              >
                <h1 className="event-grid-title">{data.content_title}</h1>
              </Col>
              <Col lg={12} className="m-0 p-0">
                {data?.content_tags?.map((tags) => {
                  return (
                    <Badge
                      className="pill-cursor m-1"
                      bg="primary"
                      pill
                      key={uuidv4()}
                      onClick={() => onSubmit({ Search: tags })}
                    >
                      {tags}
                    </Badge>
                  );
                })}
              </Col>
              <Col
                lg={12}
                className="m-0 p-0"
                dangerouslySetInnerHTML={{
                  __html: data.content_description.content_description,
                }}
              ></Col>
              <Col xs={4} className="d-lg-none d-md-none">
                {/*Date box*/}
                <Container className="event-date-box w-100 p-0 m-0">
                  <Row className="h-100 w-100 p-0 m-0">
                    <Col className="h-75 text-center">
                      <h1 className="event-date-day m-0">
                        {dateFormat(data.content_schedule, "dd")}
                      </h1>
                    </Col>
                    <Col className="h-25 text-center">
                      <h1 className="event-date-month-year m-0">
                        {dateFormat(data.content_schedule, "mmm")}{" "}
                        {dateFormat(data.content_schedule, "yyyy")}
                      </h1>
                    </Col>
                  </Row>
                </Container>
              </Col>
              <Col lg={12} xs={8} md={12} className="m-0 p-0">
                <p
                  id={`${
                    isMobile ? "event-grid-date-small" : "event-grid-date"
                  }`}
                >
                  <FontAwesomeIcon icon={faClock}></FontAwesomeIcon>{" "}
                  {data.content_start_time} - {data.content_end_time}
                </p>
                <p
                  id={`${
                    isMobile ? "event-grid-address-small" : "event-grid-address"
                  }`}
                >
                  <FontAwesomeIcon icon={faLocationPin}></FontAwesomeIcon>{" "}
                  {data.content_location}
                </p>
              </Col>
              <Col
                lg={12}
                className="m-0 p-0 d-flex justify-content-center justify-content-lg-start"
              >
                <Button
                  size="lg"
                  className="event-grid-button mb-5"
                  onClick={(e) => eventGoTo(e)}
                >
                  View Event
                </Button>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
    </>
  );
}
